.list{
   
    background-color: rgb(24, 59, 99)!important;
    color:rgb(211, 144, 102)!important;
    font-weight: bold !important;
    margin-right: 2px;
    border-radius: 25px;
    
}
.menu{
    
    color: rgb(24, 59, 99);
}
.fa{
    font-size: 30px !important;
    margin-right: 10px;
}

.search{
    width:400px !important;
    margin-left: 15%;
    border-radius: 15px;
}
.logo{

    max-width:150px;
    max-height: 150px;
    margin-left:20px;

}
.searchBtn{
    background-color:rgb(24, 59, 99) !important;
    color: rgb(211, 144, 102) !important;
    font-weight:bold!important;
    border-radius: 25px !important;
}
#dropdown-basic-button{
    color: rgb(211, 144, 102)!important;
    font-weight:bold!important;
    background-color:rgb(24, 59, 99) !important; 
    border-style: none !important;
    /* background: transparent !important; */
    font-size: 18px;
    border-radius: 25px;
}

.nav-cls {
    height: 200px !important;
    
}

@media screen and (max-width: 640px) {
    .search{
        margin-left: 0px;
        width:300px !important;
           
    }
    .nav-cls {
       height: auto;
        
    }
    .aboutUsBtn{
        display:none
    }
    .logo{
        max-width:100px;
        max-height: 100px;
        margin-left:5px;

    }
    .menu{
        
        /* top layer */
        z-index: 9999;

    }
    #sidebar {
        min-width: 250px;
        max-width: 250px;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        /* top layer */
        z-index: 9999;
    }
    #dismiss {
        width: 35px;
        height: 35px;
        position: absolute;
        /* top right corner of the sidebar */
        top: 10px;
        right: 10px;
    }
    .searchBtn{
        padding: 7px!important;
    }
    
    
    
    
}
@media screen and (min-width: 640px) {
    .menu{
        display: none;
    }
    
}



